_ui_search_input {\r\n    padding: 6px 10px 6px 30px;\r\n    border-radius: 4px;\r\n    border: 1px solid rgba(33,35,44,0.24);\r\n    font-size: 14px;\r\n    font-weight: 500;\r\n    width: 100%;\r\n}\r\n\r\n.cnx__ins__ui_search_input:focus {\r\n    outline: none;\r\n    border-color: rgba(25, 114, 247, 0.48);\r\n}\r\n\r\n.cnx__ins__ui_search_icon {\r\n    position: absolute;\r\n    top: 50%;\r\n    left: 10px;\r\n    transform: translateY(-50%);\r\n    color: rgba(33,35,44,0.24);\r\n}\r\n\r\n.cnx__ins__ui_search_icon_svg {\r\n    width: 14px;\r\n    height: 14px;\r\n}\r\n\r\n.cnx__ins__ui_search_input:focus + .cnx__ins__ui_search_icon > .cnx__ins__ui_search_icon_svg {\r\n    fill: #1972f7;\r\n}\r\n\r\n/* button */\r\n\r\n.cnx__btn {\r\n    display: inline-block;\r\n    padding: 10px 20px;\r\n    font-size: 16px;\r\n    font-weight: 600;\r\n    -webkit-text-decoration: none;\r\n    text-decoration: none;\r\n    transition: all 0.3s ease;\r\n    border: 1px solid #1967f8;\r\n    border-radius: 4px;\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 6px;\r\n    white-space: nowrap;\r\n}\r\n\r\n.cnx__btn > .cnx__btn_icon {\r\n    font-size: 18px;\r\n    padding: 0;\r\n    margin: 0;\r\n}\r\n\r\n.cnx__btn:Hover {\r\n    cursor: pointer;\r\n    background-color: rgba(0,0,0, .8);\r\n    border-color: rgba(0,0,0, .8);\r\n}\r\n\r\n/* button veriant */\r\n\r\n/* primary */\r\n\r\n.cnx__btn_primary {\r\n    background-color: #1967f8;\r\n    border-color: #1967f8;\r\n    color: #fff;\r\n}\r\n\r\n.cnx__btn_primary:hover {\r\n    background-color: rgba(0,0,0, .8);\r\n    border-color: rgba(0,0,0, .8);\r\n}\r\n\r\n.cnx__btn_primary > .cnx__btn_icon {\r\n    color: #fff;\r\n}\r\n\r\n/* secondary */\r\n\r\n.cnx__btn_secondary {\r\n    background-color: #e4e4e4;\r\n    border-color: #e4e4e4;\r\n    color: #000;\r\n}\r\n\r\n.cnx__btn_secondary:hover {\r\n    background-color: rgba(0,0,0, .8);\r\n    color: #fff;\r\n    border-color: rgba(0,0,0, .8);\r\n}\r\n\r\n.cnx__btn_secondary > .cnx__btn_icon {\r\n    color: #000;\r\n}\r\n\r\n/* tertiary */\r\n\r\n.cnx__btn_tertiary {\r\n    background-color: #fff;\r\n    border-color: #dadada;\r\n    color: #383838;\r\n}\r\n\r\n.cnx__btn_tertiary:hover {\r\n    background-color: #f1f1f1;\r\n    border-color: #dadada;\r\n}\r\n\r\n/* success */\r\n\r\n.cnx__btn_success {\r\n    background-color: #01a545;\r\n    border-color: #01a545;\r\n    color: #fff;\r\n}\r\n\r\n.cnx__btn_success:hover {\r\n    background-color: #018839;\r\n    border-color: #018839;\r\n}\r\n\r\n.cnx__btn_tertiary > .cnx__btn_icon {\r\n    color: #383838;\r\n}\r\n\r\n/* button sizes */\r\n\r\n.cnx__btn_lg {\r\n    padding: 15px 30px;\r\n    font-size: 18px;\r\n}\r\n\r\n.cnx__btn_lg > .cnx__btn_icon {\r\n    font-size: 20px;\r\n}\r\n\r\n.cnx__btn_md {\r\n    padding: 10px 20px;\r\n    font-size: 16px;\r\n}\r\n\r\n.cnx__btn_md > .cnx__btn_icon {\r\n    font-size: 16px;\r\n}\r\n\r\n.cnx__btn_sm {\r\n    padding: 5px 10px;\r\n    font-size: 14px;\r\n}\r\n\r\n.cnx__btn_sm > .cnx__btn_icon {\r\n    font-size: 14px;\r\n}\r\n\r\n/* disable */\r\n\r\n.cnx__btn_disabled:disabled {\r\n    background-color: #e4e4e4;\r\n    border-color: #e4e4e4;\r\n    color: #000;\r\n    cursor: not-allowed;\r\n}\r\n\r\n.cnx__btn_success.cnx__btn_disabled:disabled {\r\n    background-color: #3e9763;\r\n    border-color: #01a545;\r\n    color: #fff;\r\n    cursor: not-allowed;\r\n    opacity: 0.5;\r\n}\r\n\r\n/* tooltip */\r\n\r\n.cnx__ins__ui_tooltip {\r\n    z-index: 999;\r\n    background-color: #303030;\r\n    color: #ececec;\r\n    font-size: 12px;\r\n    padding: 5px 10px;\r\n    border-radius: 4px;\r\n}\r\n\r\n/* tooltip arrow */\r\n\r\n.cnx__ins__ui_tooltip_arrow {\r\n    width: 0;\r\n    height: 0;\r\n    border-style: solid;\r\n    border-width: 0 5px 5px 5px;\r\n    border-color: transparent transparent #303030 transparent;\r\n    position: absolute;\r\n    top: -5px;\r\n    left: 50%;\r\n    margin-left: -5px;\r\n}\r\n\r\n.cnx_modal {\r\n    width: 100vw;\r\n    height: 100vh;\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    background-color: rgba(0,0,0,0.2);\r\n    z-index: 999;\r\n}\r\n\r\n/* card */\r\n\r\n.cnx_card {\r\n    width: 100%;\r\n    height: -moz-fit-content;\r\n    height: fit-content;\r\n    background-color: #fff;\r\n    box-shadow: 0 0 10px 0 rgba(0,0,0,0.1);\r\n    box-sizing: border-box;\r\n    position: relative;\r\n}\r\n\r\n.cnx_card__header {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    padding: 12px;\r\n    padding-left: 16px;\r\n    height: 45px;\r\n    background-color: #f5f5f5;\r\n    border-bottom: 1px solid #e0e0e0;\r\n}\r\n\r\n.cnx_card__header > button {\r\n    background-color: transparent;\r\n    border: none;\r\n    outline: none;\r\n    cursor: pointer;\r\n    padding: 0 4px;\r\n    transition: all 0.3s ease;\r\n    border-radius: 4px;\r\n}\r\n\r\n.cnx_card__header > button > svg {\r\n    width: 18px;\r\n    height: 18px;\r\n    fill: #303030;\r\n    margin-top: 0.25rem;\r\n}\r\n\r\n.cnx_card__header > button:hover {\r\n    background-color: #e7e7e7;\r\n}\r\n\r\n.cnx_card__body {\r\n    padding: 16px;\r\n    height: 100%;\r\n}\r\n\r\n.cnx_card__footer {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    padding: 12px 16px;\r\n    height: 45px;\r\n    background-color: #f5f5f5;\r\n    border-top: 1px solid #e0e0e0;\r\n}\r\n\r\n.cnx_range_date_wrapper {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 10px;\r\n    width: 100%;\r\n}\r\n\r\n.cnx_range_date{\r\n    min-width: 100%;\r\n    padding: 7px 10px;\r\n    border-radius: 4px;\r\n    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.05);\r\n    border: 1px solid #cecece;\r\n    outline: none;\r\n}\r\n\r\n.cnx_range_date_clear {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    border-radius: 50%;\r\n    cursor: pointer;\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.cnx_range_date_clear::after {\r\n    color: black !important;\r\n    font-size: 20px !important;\r\n    background: transparent !important;\r\n}\r\n\r\n.cnx_range_date_clear::after {\r\n    padding: 0;\r\n}\r\n\r\n/* accordion menu */\r\n\r\n.cnx_accordion {\r\n    position: relative;\r\n}\r\n\r\n.cnx_accordion_item_header {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    padding: 6px 10px;\r\n    font-size: 15px;\r\n    font-weight: 500;\r\n}\r\n\r\n/* icon  */\r\n\r\n.cnx_accordion_item_header_icon {\r\n    font-size: 18px;\r\n}\r\n\r\n/* accordion menu body */\r\n\r\n.cnx_accordion_item_body {\r\n    font-size: 14px;\r\n    line-height: 1.5;\r\n    color: #666;\r\n}\r\n\r\n/* accordion menu body text */\r\n\r\n.cnx_accordion_item_body_text {\r\n    margin-bottom: 0;\r\n}\r\n\r\n.cnx__text {\r\n    display: flex;\r\n    align-items: center;\r\n}\r\n\r\n.cnx__text > span {\r\n    width: -moz-fit-content !important;\r\n    width: fit-content !important;\r\n}\r\n\r\n.cnx__text > span {\r\n    display: inline-block;\r\n}\r\n\r\n.cnx__text > span.__highlight {\r\n    color: #000;\r\n    font-weight: 600;\r\n    background: #FFFF00;\r\n}\r\n\r\n/* form */\r\n\r\n.cnx__ins__label {\r\n    display: flex;\r\n    flex-direction: column;\r\n    margin-bottom: 5px;\r\n    font-size: 14px;\r\n    color: #575757;\r\n    font-weight: 500;\r\n    gap: 6px;\r\n}\r\n\r\n.cnx__ins__input {\r\n    width: 100%;\r\n    padding: 7px 10px;\r\n    border: 1px solid #ccc;\r\n    border-radius: 4px;\r\n    box-sizing: border-box;\r\n    outline: none;\r\n}\r\n\r\n.cnx__ins__input:focus {\r\n    border: 1px solid #1153e2;\r\n}\r\n\r\n/* components */\r\n\r\n.cnx_ins__sidebar {\r\n    width: 250px;\r\n    min-width: 250px;\r\n    background-color: #fff;\r\n    border-right: 1px solid #e6e6e6;\r\n    box-sizing: border-box;\r\n    position: relative;\r\n    overflow: hidden;\r\n    overflow-y: auto;\r\n}\r\n\r\n.cnx_ins__sidebar_header {\r\n    position: sticky;\r\n    top: 0;\r\n    left: 0;\r\n    z-index: 2;\r\n    width: 100%;\r\n    height: 60px;\r\n    padding: 10px;\r\n    background-color: #fff;\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 10px;\r\n}\r\n\r\n.cnx_ins__sidebar_btn {\r\n    padding: 7px 10px;\r\n    background-color: #1d8603;\r\n    border-color: #1d8603;\r\n}\r\n\r\n.cnx_ins__sidebar_header_dd {\r\n    min-width: 180px;\r\n    padding: 5px;\r\n}\r\n\r\n.cnx_ins__sidebar_header_dd_item {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: .5rem;\r\n    padding: 7px 10px;\r\n    cursor: pointer;\r\n    border-radius: 5px;\r\n    white-space: nowrap;\r\n}\r\n\r\n.cnx_ins__sidebar_header_dd_item > i {\r\n    font-size: 16px;\r\n}\r\n\r\n.cnx_ins__sidebar_header_dd_item > span {\r\n    font-weight: 500;\r\n}\r\n\r\n.cnx_ins__sidebar_header_dd_item:hover {\r\n    color: #fff;\r\n    background-color: #086de0;\r\n}\r\n\r\n.cnx_ins__sidebar_header_dd_item:hover > svg {\r\n    fill: #fff;\r\n}\r\n\r\n/* dashboard content */\r\n\r\n.cnx_ins__sidebar_content {\r\n    overflow-y: auto;\r\n}\r\n\r\n.cnx_ins__sidebar_dashboards_header {\r\n    width: 100%;\r\n    display: flex;\r\n    align-items: center;\r\n    padding: 7px 10px;\r\n    justify-content: space-between;\r\n}\r\n\r\n.cnx_ins__sidebar_dashboards_header > .__accordion {\r\n    padding: 0;\r\n    width: 100%;\r\n    cursor: pointer;\r\n    -webkit-user-select: none;\r\n    -moz-user-select: none;\r\n    user-select: none;\r\n}\r\n\r\n.cnx_ins__sidebar_dashboards_header.__inner {\r\n    padding-left: 20px;\r\n    width: 100%;\r\n    cursor: pointer;\r\n    -webkit-user-select: none;\r\n    -moz-user-select: none;\r\n    user-select: none;\r\n    color: #555555;\r\n}\r\n\r\n.cnx_ins__sidebar_dashboards_title {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 10px;\r\n    font-size: 14px;\r\n    font-weight: 600;\r\n}\r\n\r\n.cnx_ins__sidebar_dashboards_title > i.fa-chevron-down,\r\n.cnx_ins__sideba